.container {
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
  margin-left: -20px;
}

.col {
  padding-right: 20px;
  padding-left: 20px;
  flex: 0 0 50%;
  max-width: 50%;
}

.squares {
  width: 100%;
  height: 100vh;
  background: #b49d9d;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.squares__item {
  width: 250px;
  height: 250px;
  background: #000;
  position: absolute;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  z-index: 1000;
}

.squares__item_1 {
  top: calc(50% - 250px);
  left: calc(50% - 250px);
}

.squares__item_2 {
  top: calc(50% - 250px);
  left: calc(50%);
}

.squares__item_3 {
  top: calc(50%);
  left: calc(50% - 250px);
}

.squares__item_4 {
  top: calc(50%);
  left: calc(50%);
}

.squares__circle {
  width: 250px;
  height: 250px;
  border-radius: 250px;
  background: #000;
  transition: all 0.5s;
  opacity: 0;
  transform: rotate(180deg);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
  cursor: pointer;
  font-size: 25px;
}

.form {
  width: 100%;
  height: 100vh;
  background: #9eb49d;
  position: relative;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form__content {
  display: flex;
  margin-bottom: 25px;
}

.form__content select, .form__content input {
  width: 150px;
  margin: 0 15px;
}

.form__btn {
  margin-bottom: 25px;
  visibility: hidden;
  display: flex;
  flex-direction: column;
}

.form__btn button {
  margin-bottom: 10px;
}

